 import React from 'react'

import {  BrowserRouter as Router,
          Route,
          Switch,
          Redirect } from 'react-router-dom';

import { Provider } from "react-redux"; 
import store  from "./store/store"
import Home from './pages/Home';
import './App.css';

function App() {
  return (
    <Provider store={store}> 
      <Router>
        <Switch> 
          <Route exact path="/" component={Home} />
          <footer>
                <div style={{backgroundColor: 'black'}}><p style={{color: 'gray', textAlign: 'center', margin: '0px 5px'}}>Copyright © 2020 developed and designed by ismail HOCINE</p></div>
            </footer>
        </Switch>
      </Router>
    </Provider>
  );
}

export default App;
